"use client";

import {
  Box,
  Button,
  Card,
  CardContent,
  CardMedia,
  IconButton,
  Modal,
  Rating,
  Typography,
  Snackbar,
  Alert,
} from "@mui/material";
import { useRef, useState } from "react";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import ArrowForwardIcon from "@mui/icons-material/ArrowForward";
import CloseIcon from "@mui/icons-material/Close";

interface Flower {
  name: { en: string; ka: string };
  priceUSD: number;
  img: string;
}

interface BestSellersProps {
  currency: "USD" | "GEL";
  language: "en" | "ka";
  gelToUsdRate?: number;
}

const flowers: Flower[] = [
  { name: { en: "Rose", ka: "ვარდი" }, priceUSD: 15, img: "roses.jpg" },
  { name: { en: "Lily", ka: "შროშანი" }, priceUSD: 10, img: "lily.jpg" },
  { name: { en: "Tulip", ka: "ტიტა" }, priceUSD: 12, img: "tulip.jpg" },
  { name: { en: "Daisy", ka: "გვირილა" }, priceUSD: 11, img: "daisy.jpg" },
  { name: { en: "Chrysanthemum", ka: "ქრიზანთემა" }, priceUSD: 9, img: "Chrysanthemum.jpg" },
  { name: { en: "Carnation", ka: "მიხაკი" }, priceUSD: 18, img: "Carnation.jpg" },
  { name: { en: "Lavender", ka: "ლავანდა" }, priceUSD: 6, img: "Lavender.jpg" },
  { name: { en: "Orchid", ka: "ორქიდეა" }, priceUSD: 11, img: "Orchid.jpg" },
  { name: { en: "Sunflower", ka: "მზეაყვავილა" }, priceUSD: 12, img: "Sunflower.jpg" },
  { name: { en: "Bouquet", ka: "თაიგული" }, priceUSD: 22, img: "bouquet2.jpg" },
  { name: { en: "Sun Kiss", ka: "მზის კოცნა" }, priceUSD: 19, img: "bouquet1.jpg" },
  { name: { en: "Spring", ka: "გაზაფხული" }, priceUSD: 25, img: "bouquet3.jpg" },
];

const BestSellers = ({
  currency,
  language,
  gelToUsdRate = 3,
}: BestSellersProps) => {
  const scrollRef = useRef<HTMLDivElement>(null);
  const [open, setOpen] = useState(false);
  const [selectedFlower, setSelectedFlower] = useState<Flower | null>(null);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");

  const scroll = (direction: "left" | "right") => {
    if (scrollRef.current) {
      const scrollAmount = direction === "left" ? -300 : 300;
      scrollRef.current.scrollBy({ left: scrollAmount, behavior: "smooth" });
    }
  };

  const handleOpen = (flower: Flower) => {
    setSelectedFlower(flower);
    setOpen(true);
  };

  const handleClose = () => setOpen(false);

  const getPriceString = (priceUSD: number) => {
    if (currency === "USD") return `$${priceUSD}`;
    return `${Math.round(priceUSD * gelToUsdRate)}₾`;
  };

  const handleAddToCart = (flower: Flower) => {
    const cart = JSON.parse(localStorage.getItem("cart") || "[]") as Flower[];
    cart.push(flower);
    localStorage.setItem("cart", JSON.stringify(cart));

    setSnackbarMessage(`${flower.name[language]} added to cart!`);
    setSnackbarOpen(true);
  };

  return (
    <Box
      sx={{
        width: "100%",
        height: 600,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        gap: 5,
        position: "relative",
      }}
    >
      <Typography
        letterSpacing={5}
        color="secondary"
        sx={{ fontWeight: 700, fontFamily: "serif", fontSize: { xs: 30, md: 60 } }}
      >
        BEST SELLERS:
      </Typography>

      {/* Left Arrow */}
      <IconButton
        disableFocusRipple
        disableRipple
        onClick={() => scroll("left")}
        sx={{
          position: "absolute",
          left: 10,
          top: "50%",
          transform: "translateY(-50%)",
          zIndex: 2,
          backgroundColor: "white",
          boxShadow: 2,
          "&:hover": { backgroundColor: "#ffe6f0" },
        }}
      >
        <ArrowBackIcon sx={{ fontSize: 40, p: 1 }} />
      </IconButton>

      {/* Scrollable Box */}
      <Box
        ref={scrollRef}
        sx={{
          display: "flex",
          overflowX: "auto",
          overflowY: "hidden",
          scrollbarWidth: "none",
          msOverflowStyle: "none",
          width: "90%",
          gap: 2,
          p: 1,
          scrollBehavior: "smooth",
          "&::-webkit-scrollbar": { display: "none" },
        }}
      >
        {flowers.map((item, index) => (
          <Card
            onClick={() => handleOpen(item)}
            key={index}
            sx={{
              flexShrink: 0,
              width: 300,
              height: 400,
              borderRadius: 4,
              boxShadow: 4,
              transition: "0.3s ease",
              "&:hover": {
                transform: "scale(1.03)",
                boxShadow: 10,
              },
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-between",
            }}
          >
            <CardMedia component="img" height="230" image={item.img} alt={item.name[language]} />
            <CardContent sx={{ flexGrow: 1 }}>
              <Typography
                variant="h6"
                gutterBottom
                sx={{ fontWeight: "bold", fontFamily: "Georgia, serif" }}
              >
                {item.name[language]}
              </Typography>

              <Box
                sx={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                  mt: 2,
                }}
                onClick={(e) => e.stopPropagation()} // prevent card click when clicking buttons
              >
                <Typography
                  variant="h6"
                  sx={{ color: "crimson", fontWeight: "bold", fontSize: "1.2rem" }}
                >
                  {getPriceString(item.priceUSD)}
                </Typography>

                <Box sx={{ display: "flex", gap: 1 }}>
                  <Button
                    variant="outlined"
                    sx={{
                      borderColor: "pink",
                      color: "pink",
                      textTransform: "none",
                      fontWeight: "bold",
                      "&:hover": {
                        borderColor: "#ff4081",
                        backgroundColor: "#ffe6f0",
                        color: "#ff4081",
                      },
                    }}
                    onClick={() => handleAddToCart(item)}
                  >
                    Buy
                  </Button>

                  <Button
                    variant="contained"
                    color="secondary"
                    sx={{
                      textTransform: "none",
                      fontWeight: "bold",
                      bgcolor: "pink",
                      "&:hover": {
                        bgcolor: "#ff4081",
                      },
                    }}
                    onClick={() => handleAddToCart(item)}
                  >
                    Add to Cart
                  </Button>
                </Box>
              </Box>
            </CardContent>
          </Card>
        ))}
      </Box>

      {/* Right Arrow */}
      <IconButton
        disableFocusRipple
        disableRipple
        onClick={() => scroll("right")}
        sx={{
          position: "absolute",
          right: 10,
          top: "50%",
          transform: "translateY(-50%)",
          zIndex: 2,
          backgroundColor: "white",
          boxShadow: 2,
          "&:hover": { backgroundColor: "#ffe6f0" },
        }}
      >
        <ArrowForwardIcon sx={{ fontSize: 40, p: 1 }} />
      </IconButton>

      {/* Modal */}
      <Modal open={open} onClose={handleClose}>
        <Box
          sx={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            width: { xs: 350, md: 600 },
            bgcolor: "white",
            borderRadius: 4,
            boxShadow: 24,
            p: 4,
            outline: "none",
            display: "flex",
            alignItems: "center",
            flexDirection: "column",
          }}
        >
          {/* Close Button */}
          <IconButton
            disableFocusRipple
            disableRipple
            onClick={handleClose}
            sx={{
              position: "absolute",
              top: 0,
              right: 0,
            }}
          >
            <CloseIcon
              sx={{
                fontSize: 50,
                p: 1,
                color: "grey",
                "&:hover": {
                  color: "black",
                },
              }}
            />
          </IconButton>

          {selectedFlower && (
            <>
              <Typography variant="h5" sx={{ fontWeight: "bold", mb: 2 }}>
                {selectedFlower.name[language]}
              </Typography>
              <CardMedia
                component="img"
                image={selectedFlower.img}
                alt={selectedFlower.name[language]}
                sx={{
                  mb: 2,
                  width: { xs: 300, md: 400 },
                  height: { xs: 300, md: 400 },
                }}
              />
              <Rating value={4.5} precision={0.5} readOnly sx={{ fontSize: 45, height: 40 }} />
              <Typography variant="h6">Price: {getPriceString(selectedFlower.priceUSD)}</Typography>
              <Button
                variant="outlined"
                fullWidth
                sx={{
                  borderRadius: 10,
                  mb: 1,
                  height: 50,
                  color: "#e91e63",
                  borderColor: "#f8bbd0",
                  backgroundColor: "#fff0f5",
                  transition: "0.3s ease",
                  "&:hover": {
                    backgroundColor: "#fce4ec",
                    borderColor: "#f48fb1",
                    color: "#d81b60",
                  },
                }}
                onClick={() => {
                  if (selectedFlower) handleAddToCart(selectedFlower);
                }}
              >
                buy now
              </Button>
            </>
          )}
        </Box>
      </Modal>

      <Snackbar
        open={snackbarOpen}
        autoHideDuration={3000}
        onClose={() => setSnackbarOpen(false)}
        anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
        sx={{
          left: "50%",
          transform: "translateX(-50%)",
          bottom: { xs: 70, sm: 50 },
          width: "auto",
        }}
      >
        <Alert onClose={() => setSnackbarOpen(false)} severity="success" variant="filled" sx={{ width: "100%" }}>
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default BestSellers;
